<template>
    <transition v-if="showModalAttr" name="modal">
        <div class="HomeModal">
            <div class="modal-wrapper">
            <div class="modal-container">

                <div class="modal-header">
                <slot name="header">
                    <input type="text" name="board_name" v-model="nameOfBoard" placeholder="Dodaj nazwę tablicy" />
                </slot>
                </div>

                <div class="modal-footer">
                <slot name="footer">
                    <button class="modal-default-button" @click="addBoard">
                    Utwórz tablicę
                    </button>
                    <button class="modal-default-button" @click="close">
                    Zamknij
                    </button>
                </slot>
                </div>
            </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
  name: 'HomeModal',
  data: function(){
        return {
            nameOfBoard: '',
            showModalAttr: false
        }
    },
    methods:{
      addBoard(){
        this.closeModal();
        this.$emit('addBoard', this.nameOfBoard);
        this.nameOfBoard = '';
      },
      close(){
        this.closeModal();
        this.nameOfBoard = '';
      },
      showModal(){
        this.showModalAttr = true;
      },
      closeModal(){
        this.showModalAttr = false;
      }
    }
}
</script>

<style scoped>
.HomeModal {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }
  
  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }
  
  .modal-container {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }
  
  .modal-header h3 {
    margin-top: 0;
    color: #42b983;
  }
  
  .modal-body {
    margin: 20px 0;
  }
  
  .modal-default-button {
    float: right;
  }
  
  .modal-enter {
    opacity: 0;
  }
  
  .modal-leave-active {
    opacity: 0;
  }
  
  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
</style>



